import React from "react"
import Layout from '../../containers/layout'
import Container from '../../containers/QiMen'
import { Heading } from 'grommet';
import ChartList from '../../cm-lib/reference/DetailChart.json'
import Ju from '../../cm-lib/reference/Ju.json'
import { getMonthChart } from '../../cm-lib/QiMen'
import { DEAULT_DATE_FORMAT, BRANCH, DATETIME_FORMAT, SEARCH_PERIOD_IN_MONTH, DIRECTION } from '../../cm-lib/constants'
import { format, parseISO, addMonths, differenceInMonths } from 'date-fns';
import Common from './common'

const search = (chart, palace) => {
    const thisMonth = new Date();
    const searchEndDate = addMonths(thisMonth, SEARCH_PERIOD_IN_MONTH);
    const diff = differenceInMonths(searchEndDate, thisMonth);
    return [...Array(diff).keys()].map(d => {
        const compareDate = addMonths(thisMonth, d);
        const key = format(compareDate, DEAULT_DATE_FORMAT);
        const chartList = getMonthChart(compareDate.getFullYear(), compareDate.getMonth() + 1, compareDate.getDate());
        return [chartList]
        .reduce((a, e) => {
            const formationIndex = chart.findIndex(o => e.structure === o.structure && e.pillar === o.hour)
            
            if(formationIndex > -1  && palace.includes(DIRECTION[chart[formationIndex].e.i])) {
 
                return a.concat([{
                    ...e,
                    f: { ...chart[formationIndex].e, d: DIRECTION[formationIndex] }
                }])
            }
            return a;
        }
        , [])
        .map(ee =>  {
            const hour = BRANCH.indexOf(ee.pillar.substr(1, 1)) * 2;
            return { ...ee, date: `${key}T00:00:00`};
        })
    })
    .reduce((a, n) => a.concat(n), [])
  }

const handleSearch = (searchFormation, palace) => {
    const foundYin = Ju.filter(v => v.structure.substr(0, 1) === 'F' && v.chart.find(g => g.f && g.f.indexOf(searchFormation) > -1 ))
    .map(e => ({ structure: e.structure, hour: e.hour, e: { ...e.chart.find(e => e.f === searchFormation), i: e.chart.findIndex(e => e.f === searchFormation) }}))
    const result = search(foundYin, palace);
    const display = (v, square) => `${format(parseISO(`${v}`), DATETIME_FORMAT)} ${square.heaven} ${square.star} ${square.door} ${square.god} ${square.earth} ${DIRECTION[square.i]}`
    return result.map((v, i) => ({ ...v, display: display(v.date, v.f)}))
  }

const getChart = (dateObject, updateChart) => {

  const y = format(dateObject, 'yyyy')
  const m = format(dateObject, 'M')
  const d = format(dateObject, 'd')
  const ch = getMonthChart(y, m, d);
  const foundChart = Ju.find(e => e.structure === ch.structure && e.hour === ch.pillar);
  updateChart(foundChart);
}  

const Home = () => {

  return <Common
    heading="Qi Men Dun Jia Month Chart"
    handleSearch={handleSearch} 
    onDateChanged={getChart}
  />
}

export default Home
